.Button {
    padding: 14px 27px;
    border-radius: 30px;
    outline: none;
    cursor: pointer;
    font-size: 17px;
    margin: 10px;
}

.Button.red {
    background-color: rgba(0, 89, 255, .2);
    border: solid 1px #7090ff;
    color: #7090ff;
}

.Button.blue {
    background-color: rgba(255, 78, 78, .2);
    border: solid 1px #ff4664;
    color: #ff4664;
}